import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, ParamMap } from '@angular/router';
import { PutnhanvienService } from './putnhanvien.service';
import { Router } from '@angular/router';
import { NhanvienService } from '../nhanvien/nhanvien.service';
import { NhanvienComponent } from '../nhanvien/nhanvien.component';

@Component({
  selector: 'app-putnhanvien',
  templateUrl: './putnhanvien.component.html',
  styleUrls: ['./putnhanvien.component.css'],
  providers: [PutnhanvienService, NhanvienComponent]
})
export class PutnhanvienComponent implements OnInit {

  constructor(private route: ActivatedRoute , private put: PutnhanvienService, private router: Router,
    private nhanvienService: NhanvienService, private nhanviencom: NhanvienComponent) { }
  id;
  name;
  age;
  hometown;
  check;
  ngOnInit() {
    this.route.paramMap.subscribe((params: ParamMap) => {
      this.id = params.get('id');
      this.name = params.get('name');
      this.age = params.get('age');
      this.hometown = params.get('hometown');
    });
  }
  onsubmit(formputNhanvien) {
    this.check = confirm('Ban co chac chan muon thay doi');
    if (this.check === true) {
      this.put.sendPut(this.id, formputNhanvien.value)
      .then(result => console.log(result))
      .catch(err => console.log(err));
      this.nhanvienService.getNhanvien().subscribe(res => this.nhanviencom.nhanvien = res);
      this.router.navigate(['/getnhanvien']);
    } else {
      this.router.navigate(['/getnhanvien']);
    }
  }
}



