import { Component, OnInit, ViewChild } from '@angular/core';
import { PostnhanvienService } from './postnhanvien.service';
import { Router } from '@angular/router';
import { NhanvienService } from '../nhanvien/nhanvien.service';
import { NhanvienComponent } from '../nhanvien/nhanvien.component';

@Component({
  selector: 'app-postnhanvien',
  templateUrl: './postnhanvien.component.html',
  styleUrls: ['./postnhanvien.component.css'],
  providers: [PostnhanvienService, NhanvienComponent]
})
export class PostnhanvienComponent implements OnInit {
  constructor(private post: PostnhanvienService, private router: Router,
  private nhanvienService: NhanvienService, private nhanviencom: NhanvienComponent) { }

  ngOnInit() {
  }
  onsubmit(formNhanvien) {
    this.post.sendPost(formNhanvien.value)
    .then(result => console.log(result))
    .catch(err => console.log(err));
    this.nhanvienService.getNhanvien().subscribe(res => this.nhanviencom.nhanvien = res);
    this.router.navigate(['/getnhanvien']);
  }
}



