import { Component, OnInit } from '@angular/core';
import { NhanvienService } from './nhanvien.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-nhanvien',
  templateUrl: './nhanvien.component.html',
  styleUrls: ['./nhanvien.component.css']
})
export class NhanvienComponent implements OnInit {
  constructor(private nhanvienService: NhanvienService, private router: Router) { }
  nhanvien;
  check;
  id;
  ngOnInit() {
    this.nhanvienService.getNhanvien().subscribe(res => this.nhanvien = res);
  }
  deletenv(id) {
    this.check = confirm('Ban co chac chan muon xoa');
    if (this.check === true) {
      this.nhanvienService.deleteNhanvien(id).subscribe(data => {
        this.nhanvienService.getNhanvien().subscribe(res => this.nhanvien = res);
        this.router.navigate(['/getnhanvien']);
      });
    } else {
      this.router.navigate(['/getnhanvien']);
    }
  }
}



